import React, {useState} from 'react';
import Thumbs from '../images/iconfinder_like_1814076.svg';

function Like() {
    const [count, setCount] = useState(0);
    
    return (
        <div>
            <p>Number of Likes:{count}</p>
           <img id='likes' src={Thumbs} alt="like button" onClick={() => setCount(count + 1)} />
           Click Me!
        </div>
    )
}



export default Like